import React, { useState } from "react";
import Navbar from "./components/Navbar";
import News from "./components/News";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import LoadingBar from "react-top-loading-bar";

const App = () => {
  const apiKey = process.env.REACT_APP_NEWS_API;
  const [progress, setProgress] = useState(5);
  return (
    <div>
      <Router>
        <Navbar />
        <LoadingBar color="#f11946" progress={progress} />
        <Switch>
          <Route exact key="news" path="/">
            {" "}
            <News setProgress={setProgress} apiKey={apiKey} topic="news" />
          </Route>
          <Route exact key="tech" path="/tech">
            {" "}
            <News setProgress={setProgress} apiKey={apiKey} topic="tech" />
          </Route>
          <Route exact key="sport" path="/sports">
            {" "}
            <News setProgress={setProgress} apiKey={apiKey} topic="sport" />
          </Route>
          <Route exact key="finance" path="/finance">
            {" "}
            <News setProgress={setProgress} apiKey={apiKey} topic="finance" />
          </Route>
          <Route exact key="politics" path="/politics">
            {" "}
            <News setProgress={setProgress} apiKey={apiKey} topic="politics" />
          </Route>
          <Route exact key="entertainment" path="/entertaiment">
            {" "}
            <News
              setProgress={setProgress}
              apiKey={apiKey}
              topic="entertainment"
            />
          </Route>
          <Route exact key="travel" path="/travel">
            {" "}
            <News setProgress={setProgress} apiKey={apiKey} topic="travel" />
          </Route>
          <Route exact key="science" path="/science">
            {" "}
            <News setProgress={setProgress} apiKey={apiKey} topic="science" />
          </Route>
        </Switch>
      </Router>
    </div>
  );
};

export default App;
