import React, { useState, useEffect } from "react";
import NewsItem from "./NewsItem";
import Spinner from "./Spinner";
import InfiniteScroll from "react-infinite-scroll-component";

const News = (props) => {
  const [articles, setArticles] = useState([]);
  const [page, setPage] = useState(1);
  // const [totalPage, setTotalPage] = useState();
  // const [pageSize, setPageSize] = useState();
  const [loading, setLoading] = useState(true);
  const [totalArticles, setTotalArticles] = useState(0);

  // const handlePrev = () => {
  //   setPage(page - 1);

  // };

  // const handleNext = () => {
  //   setPage(page + 1);
  // };

  const fetchMoreData = () => {
    setPage(page + 1);
  };

  useEffect(() => {
    props.setProgress(10);
    const fetchNewData = async () => {
      try {
        const url = `https://api.newscatcherapi.com/v2/latest_headlines?countries=IN&topic=${props.topic}&page_size=21&page=${page}`;
        setLoading(true);
        let data = await fetch(url, {
          headers: {
            "x-api-key": props.apiKey,
          },
        });
        props.setProgress(30);
        let showData = await data.json();
        // console.log(showData);
        props.setProgress(70);
        setArticles(articles.concat(showData.articles));
        setTotalArticles(showData.total_hits);
        // setTotalPage(showData.total_pages);
        // setPageSize(showData.page_size);
        setLoading(false);
        props.setProgress(100);
      } catch (error) {
        console.log("Error is :", error);
      }
    };
    fetchNewData();
  }, [page]);

  return (
    <>
      {loading && <Spinner />}
      <h1 className="my-4 text-center">{`Today's Top ${props.topic} Headlines`}</h1>
      <InfiniteScroll
        dataLength={articles.length}
        next={fetchMoreData}
        hasMore={articles.length !== totalArticles}
        loader={<Spinner />}
      >
        <div className="container">
          <div className="row">
            {articles.map((element) => {
              return (
                <div key={element.link} className="col-md-4">
                  <NewsItem
                    imgUrl={element.media}
                    title={element.title}
                    des={element.excerpt}
                    newsUrl={element.link}
                    clean_url={element.clean_url}
                    published_date={element.published_date}
                  />
                </div>
              );
            })}
          </div>
        </div>
      </InfiniteScroll>
      {/* <div className="row my-3">
        <div className="col-12 d-flex justify-content-between">
          <button
            disabled={page <= 1}
            className="btn btn-dark"
            onClick={handlePrev}
          >
            
            &larr; Previous
          </button>
          <button
            disabled={page > Math.floor(totalPage/pageSize)}
            className="btn btn-dark"
            onClick={handleNext}
          >
            Next &rarr;
          </button>
        </div>
      </div> */}
    </>
  );
};

export default News;
